import { useEffect, useRef, useState } from "react";
import {
  PaymentWidgetInstance,
  loadPaymentWidget,
} from "@tosspayments/payment-widget-sdk";
import { nanoid } from "nanoid";
import { useLocation } from "react-router-dom";
import { HyperTextOne, PaymentWrap, PriceWrap } from "./paymentStyle";
import { DefaultBt } from "../sign/up/styles/UserSignUpStyles";

const selector = "#payment-widget";
const clientKey = "test_gck_docs_Ovk5rk1EwkEbP0W43n07xlzm";
const customerKey = "YbX2HuSlsC9uVJW6NMRMj";

export function CheckoutPage() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  // Load
  const amount = Number(queryParams.get("amount"));
  const pk = Number(queryParams.get("pk"));

  console.log("amount", amount);
  console.log("pk", pk);
  // console.log(pk)

  const paymentWidgetRef = useRef<PaymentWidgetInstance | null>(null);
  const paymentMethodsWidgetRef = useRef<ReturnType<
    PaymentWidgetInstance["renderPaymentMethods"]
  > | null>(null);
  const [price, setPrice] = useState(amount);

  useEffect(() => {
    (async () => {
      const paymentWidget = await loadPaymentWidget(clientKey, customerKey);

      const paymentMethodsWidget = paymentWidget.renderPaymentMethods(
        selector,
        { value: price, currency: "KRW", country: "KR" },
        { variantKey: "DEFAULT" },
      );

      paymentWidgetRef.current = paymentWidget;
      paymentMethodsWidgetRef.current = paymentMethodsWidget;
    })();
  }, []);

  useEffect(() => {
    const paymentMethodsWidget = paymentMethodsWidgetRef.current;

    if (paymentMethodsWidget == null) {
      return;
    }

    paymentMethodsWidget.updateAmount(price);
  }, [price]);

  return (
    <PaymentWrap>
      <div style={{ padding: "20px" }}>
        <HyperTextOne>
          <h1> </h1>
        </HyperTextOne>
        <PriceWrap>
          <span>{`${price.toLocaleString()}`}</span>
        </PriceWrap>
      </div>
      {/*   */}
      {/* <div>
        <label>
          <input
            type="checkbox"
            onChange={event => {
              setPrice(event.target.checked ? price - 5_000 : price + 5_000);
            }}
          />
          5,000   
        </label>
      </div> */}
      <div id="payment-widget" />
      <div style={{ display: "flex", justifyContent: "center" }}>
        <DefaultBt
          onClick={async () => {
            const paymentWidget = paymentWidgetRef.current;

            try {
              // ## Q.        ?
              //    .     ,   API    .
              //     (`successUrl`, `failUrl`)    .
              await paymentWidget?.requestPayment({
                orderId: nanoid(),
                orderName: " ",
                customerName: " ",
                // customerEmail: "customer123@gmail.com",
                successUrl: `${window.location.origin}/payment/success?pk=${pk}`,
                failUrl: `${window.location.origin}/payment/fail`,
              });
            } catch (error) {
              // handle error
            }
          }}
        >
          <span></span>
        </DefaultBt>
      </div>
    </PaymentWrap>
  );
}
